* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}



#root {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.navbar {
  height: 50px;
  width: 100vw;
  justify-content: space-around;
  display: flex;
  align-items: center;
  padding: 0 15px;
  background: lightgray;
}

.navbar__links {
  
}

.tech{
  position: relative;
  box-sizing: border-box;
  display: grid;
  width: 700px;
  justify-items: auto;
}

.tech__content img{
  max-width: 40px;
  max-height: 40px;  
  margin-right: 10px;
}

.tech__title {
  display: 'inline-block';
  min-width: 75px; 
  box-sizing: border-box;
  justify-content: flex-start;
  margin: 10px;
}

.tech__content {
  display: flex;
  position: relative;
  font-size: 18px;
  box-sizing: border-box;
  border: 1px solid teal;
  border-collapse: collapse;
  margin-top: 20px;
  padding: 10px;
  justify-content: space-between; /* Или space-around */
  align-items: center;
}

.tech__body {
  position: sticky;
  margin-left: 10px;
  margin-right: 20px; /* Добавьте отступ справа */
  display: flex; /* Измените на flex */
  flex-grow: 1; /* Позволяет элементу занимать доступное пространство */
}
